import styled from "styled-components";
import MovieComponent from "./components/MovieComponent";
import MovieInfoComponent from "./components/MovieInfoComponent";
import { React, useState } from 'react'
import axios from "axios";

export const API_KEY = 'cebee7ed';

const Container = styled.div`
  display:flex;
  flex-direction:column;
`;
const Header = styled.div`
  display:flex;
  flex-direction:row;
  justify-content:space-between;
  align-items:center;
  background-color:black;
  color:white;
  padding:10px;
  font-size:25px;
  font-weight:600;
  box-shadow:0 3px 6px 0 #555;
`;
const AppName = styled.div`
  display:flex;
  flex-direction:row;
  align-items:center;
  width: 50%;
`;
const MovieImg = styled.img`
  width:40px;
  height:40px;
  margin:2px;
`;
const SearchBox = styled.div`
  display:flex;
  flex-direction:row;
  padding:10px 10px;
  background-color:white;
  border-radius:6px;
  margin-left:20px;
  width:50%;
`;
const SearchIcon = styled.img`
  width:25px;
  height:25px;
`;
const SearchInput = styled.input`
  color:black;
  font-weight:bold;
  outline: none;
  border:none;
`;
const MovieListContainer = styled.div`
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  padding:30px;
  justify-content: space-evenly;
`;
function App() {
  const [searchQuery, updateSearchQuery] = useState();
  const [timeoutID, updatetimeoutID] = useState();
  const [movieList, updateMovieList] = useState([]);
  const [selectedMovie, onMovieSelect] = useState();

  // API Call 
  const fetchData = async (searchString) => {
    const response = await axios.get(`https://www.omdbapi.com/?s=${searchString}&apikey=${API_KEY}`);
    updateMovieList(response.data.Search);
  }
  // API End

  const onTextChange = (e) => {
    clearTimeout(timeoutID);
    updateSearchQuery(e.target.value);
    const timeout = setTimeout(() => fetchData(e.target.value), 500);
    updatetimeoutID(timeout);
  }
  return (
    <Container>
      <Header>
        <AppName>
          <MovieImg src="movie-icon.png" />
          React Movie App
        </AppName>
        <SearchBox>
          <SearchIcon src="search-icon.png" />
          <SearchInput placeholder="Search Movie" onChange={onTextChange} value={searchQuery} />
        </SearchBox>
      </Header>

      {selectedMovie && <MovieInfoComponent selectedMovie={selectedMovie} onMovieSelect={onMovieSelect} />}

      <MovieListContainer>
        {
          movieList?.length ? movieList.map((movie, index) => <MovieComponent key={index} movie={movie} onMovieSelect={onMovieSelect} />) : "No Data Available"
        }
      </MovieListContainer>
    </Container>

  );
}

export default App;
