import axios from "axios";
import styled from "styled-components";
import React, { useState, useEffect } from 'react'

const API_KEY = 'cebee7ed';

const Container = styled.div`
    display: flex;
    flex-direction: row;
    padding: 20px 20px;
    border-bottom: 1px solid lightgray;
    flex-wrap: wrap;    
`;
const CoverImg = styled.img`
    height: 290px;
    object-fit: cover;
    margin-left:150px;
`;
const InfoColumn = styled.div`
    display:flex;
    flex-direction:column;
    margin:0 30px;
`;
const MovieName = styled.span`
    font-size:22px;
    font-weight: 600;
    color: black;
    margin:10px 0;
    white-space:nowrap;
    text-overflow: ellipsis;
    overflow: hidden;    
    text-transform: capitalize;
`;
const MovieInfo = styled.span`
    font-size:16px;
    font-weight: 500;
    color: black;
    margin: 4px 0;
    overflow: hidden;
    text-transform: capitalize;
    & span{
        opacity: 0.5;
    }
`;
const Close = styled.span`
    font-size:16px;
    font-weight: 600;
    color: black;
    background: lightgray;
    height: fit-content;
    padding: 8px;
    border-radius: 50%;
    cursor: pointer;
    opacity: 0.8;
    margin: 0 auto;
`;
const MovieInfoComponent = (props) => {
    // console.log(props);
    const [movieInfo, setMovieInfo] = useState()
    const { selectedMovie } = props;
    // console.log(selectedMovie);
    useEffect(() => {
        axios
            .get(`https://omdbapi.com/?i=${selectedMovie}&apikey=${API_KEY}`)
            .then((response) => { setMovieInfo(response.data) });
    }, [selectedMovie])
    // console.log(movieInfo);
    return (
        <Container>
            <CoverImg src={movieInfo?.Poster} />
            <InfoColumn>
                <MovieName>{movieInfo?.Type} : {movieInfo?.Title}</MovieName>
                <MovieInfo>Released Date : <span>{movieInfo?.Released}</span></MovieInfo>
                <MovieInfo>IMDB Rating : <span>{movieInfo?.imdbRating}</span></MovieInfo>
                <MovieInfo>Year : <span>{movieInfo?.Year}</span></MovieInfo>
                <MovieInfo>Runtime : <span>{movieInfo?.Runtime}</span></MovieInfo>
                <MovieInfo>Genre : <span>{movieInfo?.Genre}</span></MovieInfo>
                <MovieInfo>Director : <span>{movieInfo?.Director}</span></MovieInfo>
                <MovieInfo>Language : <span>{movieInfo?.Language}</span></MovieInfo>
                <MovieInfo>Actors : <span>{movieInfo?.Actors}</span></MovieInfo>
                <MovieInfo>Production : <span>{movieInfo?.Production}</span></MovieInfo>
            </InfoColumn>
            <Close onClick={()=> props.onMovieSelect()}>X</Close>
        </Container>
    )
}
export default MovieInfoComponent